<template>
    <div class="box">
      <van-nav-bar title="商品搜索" left-arrow @click-left="clickLeft" fixed z-index="100"  placeholder :border="false" />
      <van-search
        v-model.trim="searchVal"
        show-action
        placeholder="请输入您搜索的商品"
        @search="searchGoods(searchVal)" :autofocus="true">
        <template #action>
          <div @click="searchGoods(searchVal)" class="search-btn">搜索</div>
        </template>
      </van-search>
      <div class="history" v-if="historyList.length>0">
        <div class="top">
          <div  class="title">最近搜索</div>
          <div class="icon" @click="empty"><van-icon size="20" name="delete-o" /></div>
        </div>
        <div class="list">
          <div class="item" v-for="(item,index) in historyList" :key="index" @click="searchGoods(item)">{{ item }}</div>
        </div>
      </div>
    </div>
</template>

<script>
import { getHistory, setHistory } from '@/utils/storage'
export default {
  name: 'SearchIndex',
  data () {
    return {
      searchVal: '',
      historyList: getHistory()
    }
  },
  methods: {
    clickLeft () {
      const backUrl = this.$route.query.backUrl
      if (backUrl) {
        this.$router.replace(backUrl)
        return
      }
      this.$router.back()
    },
    searchGoods (searchVal) {
      if (!searchVal) {
        return
      }
      this.searchVal = searchVal
      const index = this.historyList.indexOf(searchVal)
      if (index !== -1) {
        this.historyList.splice(index, 1)
      }
      this.historyList.unshift(searchVal)
      setHistory(this.historyList)
      this.$router.replace(`/searchlist?goods=${searchVal}`)
    },
    empty () {
      setHistory([])
    }
  }
}
</script>

<style lang="less" scoped>
.box{
  background-color: @primaryBg;
  min-height: 100vh;
}
.van-search__action{
  background-color: @primaryColor;
  color: #fff;
  padding: 0 15px ;
}
.van-search{
  box-sizing: border-box;
  width: 100%;
  padding-right: 12px;
}
.history{
  padding: 0 12px;
  .top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0;
    .title{
      font-size: 14px;
    }
    .icon{
      width: 30px;
      height: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  .list{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;
    .item{
      padding: 3px 15px;
      background-color: #fff;
      border-radius: 20px;
      font-size: 14px;
      margin: 0 10px 10px 0;
    }
  }
}
</style>
